/* custom.css*/

.action_sign_container {
    border: 1px solid #ddd;
    border-radius: 5px 5px 0 0;
    text-align: center;
    padding: 5px;
}
.operation_variable_container {
    border: 1px solid #ddd;
    border-radius: 0 0 5px 5px;
    padding: 5px 0;
    border-top: none
}
.user_invalid {
    position: absolute;
    top: 4px;
    right: 20px;
    font-size: 12px;
    color: rgb(249, 104, 104);
}
.operation_formula_container {
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 5px 0;
    min-height: 242px;
}
.formula_head_container {
    border-bottom: 1px solid #ddd;
    padding: 5px;
    text-align: center;
}
.operations_ops {
    margin-right: 5px;
    border: 1px solid #ddd;
    background: none;
    height: 34px;
    width: 38px;
    line-height: 35px;
    text-align: center;
    padding: 0!important;
}
.operation_formula_container button, .operation_variable_container button {
    max-width: 100px;
}
.width-60-px .ant-input-number {
    width: 60px;
}
.apply_button {
    float: right;
    
}
/* custom.css*/